import { seq6, seq7} from './TestData/Sequence';
import * as bootstrap from 'bootstrap'
import { CanvasScene } from './CanvasScene';
import { SkeletonLoader } from './SkeletonLoader';
import { Skeleton } from './Entities/Skeleton';
import { PaintedSkeleton } from './Entities/PaintedSkeleton';

const dotsEffect = () => {
    const dots = document.getElementById("loading-dots")
    const length = (dots.innerText.length + 1) % 4;
    dots.innerText = ".".repeat(length);
  }
const interval = setInterval(dotsEffect, 500);


const canvasScene = new CanvasScene();

const loader = new SkeletonLoader();
const skeletons = loader.createPaintedSkeleton();

skeletons.then(res => {
    canvasScene.init(res[1], res[0])
    canvasScene.setLoaded();
    clearInterval(interval)
    canvasScene.start();
})

