import { DTWMatrix } from "./Entities/DTWMatrix";
import { Pose } from "./Entities/Pose";
import { Skeleton } from "./Entities/Skeleton";
import { WarpingStep } from "./Entities/WarpingStep";


export const calculateDTW = (mentorSkeleton: Skeleton, pupilSkeleton : Skeleton) => {

  return calculateDTWOnPoses(mentorSkeleton.getPoses(), pupilSkeleton.getPoses())
}

export const calculateDTWOnPoses = (mentorPoses: Pose[], pupilPoses: Pose[]) => {
  const warpingPaths : WarpingStep[][] = []

  for (let index = 0; index < 31; index++) {
    
    const mentorJointTimeSequence = mentorPoses.map(pose => pose.getJointByIndex(index));
    const pupilJointTimeSequence = pupilPoses.map(pose => pose.getJointByIndex(index));

    const matrix = new DTWMatrix(mentorJointTimeSequence, pupilJointTimeSequence);
    warpingPaths.push(matrix.calculateWarpingDistance())

  }

  return warpingPaths;

}

/**
 * 
 * @param warpingPaths 
 * 
 * 
 * @returns list of deltas - every nth elements represents one joint, every value in list corresponds to one frame
 */
export const calculateWarpingPathDeltas = (warpingPaths : WarpingStep[][]) => {
  const warpingPathDeltas : number[][] = []

  warpingPaths.map(warpingPath => warpingPathDeltas.push(calculateWarpingPathDelta(warpingPath)));

  return warpingPathDeltas;
}

const calculateWarpingPathDelta = (warpingPath : WarpingStep[]) => {
  const maxFrames = warpingPath[warpingPath.length - 1].pupilFrame;
  
  const deltaList = Array(maxFrames + 1)
  const poseCount = Array(maxFrames + 1)

  for (let i = 0; i <= maxFrames; i++) {
    deltaList[i] = 0;
    poseCount[i] = 0;
  }

  for (let i = 0; i < warpingPath.length; i++ ) {
    const warpingStep = warpingPath[i];
    const pupilFrame = warpingStep.pupilFrame;
    deltaList[pupilFrame] += warpingStep.distance
    poseCount[pupilFrame] += 1;
  }

  for (let i = 1  ; i <= maxFrames; i++) {
    deltaList[i] = deltaList[i] / poseCount[i];
  }

  return deltaList;

}

export const getJointFrameTracking = (warpingPath: WarpingStep[]) => {
  const maxFrames = warpingPath[warpingPath.length - 1].pupilFrame;
  
  const frameList : number[] = Array(maxFrames + 1);

  for (let i = 0; i <= maxFrames; i++) {
    frameList[i] = 0;
  }

  warpingPath.map(warpingStep => {
    frameList[warpingStep.pupilFrame] = warpingStep.mentorFrame;
  })

  return frameList;
  
};