
/**
 * Represents a single cell in DTW matrix calculation
 * 
 */
export class DTWMatrixCell {

  distanceValue : number;
  previousWarping : [number, number];
  absoluteJointDistance : number

  constructor(distanceValue) {
    this.distanceValue = distanceValue;
    this.previousWarping = [-1, -1];
    this.absoluteJointDistance = 0;
  }

  setPreviousWarping(index: [number, number]) {
    this.previousWarping = [index[0], index[1]];
  }

}