import { Vector3 } from "three";
import { ALL_PARTS, BodyParts } from "./BodyParts";
import * as THREE from 'three';

export class Pose {

  private coordinates : Vector3[];
  static ROOT_INDEX: number = 0;
  static LHIPJOINT_INDEX: number = 1;
  static LFEMUR_INDEX: number = 2;
  static LTIBIA_INDEX: number = 3;
  static LFOOT_INDEX: number = 4;
  static LTOES_INDEX: number = 5;
  static RHIPJOINT_INDEX: number = 6;
  static RFEMUR_INDEX: number = 7;
  static RTIBIA_INDEX: number = 8;
  static RFOOT_INDEX: number = 9;
  static RTOES_INDEX: number = 10;
  static LOWERBACK_INDEX: number = 11;
  static UPPERBACK_INDEX: number = 12;
  static THORAX_INDEX: number = 13;
  static LOWERNECK_INDEX: number = 14;
  static UPPERNECK_INDEX: number = 15;
  static HEAD_INDEX: number = 16;
  static LCLAVICLE_INDEX: number = 17;
  static LHUMERUS_INDEX: number = 18;
  static LRADIUS_INDEX: number = 19;
  static LWRIST_INDEX: number = 20;
  static LHAND_INDEX: number = 21;
  static LFINGERS_INDEX: number = 22;
  static LTHUMB_INDEX: number = 23;
  static RCLAVICLE_INDEX: number = 24;
  static RHUMERUS_INDEX: number = 25;
  static RRADIUS_INDEX: number = 26;
  static RWRIST_INDEX: number = 27;
  static RHAND_INDEX: number = 28;
  static RFINGERS_INDEX: number = 29;
  static RTHUMB_INDEX: number = 30;

  static TORSO_JOINTS       = [Pose.ROOT_INDEX, Pose.LOWERBACK_INDEX, Pose.UPPERBACK_INDEX, Pose.THORAX_INDEX];
  static LEFT_ARM_JOINTS    = [Pose.LCLAVICLE_INDEX, Pose.LHUMERUS_INDEX, Pose.LRADIUS_INDEX, Pose.LWRIST_INDEX, Pose.LHAND_INDEX, Pose.LFINGERS_INDEX];
  static RIGHT_ARM_JOINTS   = [Pose.RCLAVICLE_INDEX, Pose.RHUMERUS_INDEX, Pose.RRADIUS_INDEX, Pose.RWRIST_INDEX, Pose.RHAND_INDEX, Pose.RFINGERS_INDEX];
  static LEFT_LEG_JOINTS    = [Pose.LHIPJOINT_INDEX, Pose.LFEMUR_INDEX, Pose.LTIBIA_INDEX, Pose.LFOOT_INDEX, Pose.LTOES_INDEX];
  static RIGHT_LEG_JOINTS   = [Pose.RHIPJOINT_INDEX, Pose.RFEMUR_INDEX, Pose.RTIBIA_INDEX, Pose.RFOOT_INDEX, Pose.RTOES_INDEX];
  static HEAD_JOINTS        = [Pose.LOWERNECK_INDEX, Pose.UPPERNECK_INDEX, Pose.HEAD_INDEX];
  static LEFT_THUMB_JOINTS  = [Pose.LTHUMB_INDEX];
  static RIGHT_THUMB_JOINTS = [Pose.RTHUMB_INDEX];
  
  constructor(joints : Vector3[]) {
    this.coordinates = joints;
  }

  getTorso() : Array<Vector3> {
    return [ this.coordinates[Pose.ROOT_INDEX], this.coordinates[Pose.LOWERBACK_INDEX], this.coordinates[Pose.UPPERBACK_INDEX], this.coordinates[Pose.THORAX_INDEX] ]
  }

  getLeftArm() : Array<Vector3> {
    return [ this.coordinates[Pose.THORAX_INDEX], this.coordinates[Pose.LCLAVICLE_INDEX], this.coordinates[Pose.LHUMERUS_INDEX], this.coordinates[Pose.LRADIUS_INDEX], this.coordinates[Pose.LWRIST_INDEX], this.coordinates[Pose.LHAND_INDEX], this.coordinates[Pose.LFINGERS_INDEX]]
  }

  getLeftLeg() : Array<Vector3> {
    return [ this.coordinates[Pose.ROOT_INDEX], this.coordinates[Pose.LHIPJOINT_INDEX], this.coordinates[Pose.LFEMUR_INDEX], this.coordinates[Pose.LTIBIA_INDEX], this.coordinates[Pose.LFOOT_INDEX], this.coordinates[Pose.LTOES_INDEX] ]
  }

  getRightArm() : Array<Vector3> {
    return [ this.coordinates[Pose.THORAX_INDEX], this.coordinates[Pose.RCLAVICLE_INDEX], this.coordinates[Pose.RHUMERUS_INDEX], this.coordinates[Pose.RRADIUS_INDEX], this.coordinates[Pose.RWRIST_INDEX], this.coordinates[Pose.RHAND_INDEX], this.coordinates[Pose.RFINGERS_INDEX]]
  }

  getRightLeg() : Array<Vector3> {
    return [ this.coordinates[Pose.ROOT_INDEX], this.coordinates[Pose.RHIPJOINT_INDEX], this.coordinates[Pose.RFEMUR_INDEX], this.coordinates[Pose.RTIBIA_INDEX], this.coordinates[Pose.RFOOT_INDEX], this.coordinates[Pose.RTOES_INDEX] ]
  }

  getHead() : Array<Vector3> {
    return [ this.coordinates[Pose.THORAX_INDEX], this.coordinates[Pose.LOWERNECK_INDEX], this.coordinates[Pose.UPPERNECK_INDEX], this.coordinates[Pose.HEAD_INDEX] ]
  }

  getRightThumb() : Array<Vector3> {
    return [ this.coordinates[Pose.RHAND_INDEX], this.coordinates[Pose.RTHUMB_INDEX]];
  }

  getLeftThumb() : Array<Vector3> {
    return [this.coordinates[Pose.LHAND_INDEX], this.coordinates[Pose.LTHUMB_INDEX]];
  }

  getLowestPoint() : Vector3 {
    const coordinates = this.coordinates;

    return coordinates.reduce(
      (lowest, current) => { 
        if (lowest.y > current.y) {
          return current; 
        }
        return lowest;
      }, 
      this.coordinates[Pose.ROOT_INDEX]);
  }

  getJointsIndices(bodyPart : BodyParts) : number[] {
    switch(bodyPart) {
      case BodyParts.Head:
        return Pose.HEAD_JOINTS;

      case BodyParts.LeftLeg:
        return Pose.LEFT_LEG_JOINTS;

      case BodyParts.LeftArm:
        return Pose.LEFT_ARM_JOINTS;
      
      case BodyParts.RightLeg:
        return Pose.RIGHT_LEG_JOINTS;

      case BodyParts.RightArm:
        return Pose.RIGHT_ARM_JOINTS;

      case BodyParts.Torso:
        return Pose.TORSO_JOINTS;
      
      case BodyParts.LeftThumb:
        return Pose.LEFT_THUMB_JOINTS;
      
      case BodyParts.RightThumb:
        return Pose.RIGHT_THUMB_JOINTS;
      
      default:
        return [];
    }
  }

  getJointsConnectedIndices(bodyPart : BodyParts) : number[] {
    switch(bodyPart) {
      case BodyParts.Head:
        return [Pose.THORAX_INDEX].concat(Pose.HEAD_JOINTS);

      case BodyParts.LeftLeg:
        return [Pose.ROOT_INDEX].concat(Pose.LEFT_LEG_JOINTS);

      case BodyParts.LeftArm:
        return[Pose.THORAX_INDEX].concat(Pose.LEFT_ARM_JOINTS);
      
      case BodyParts.RightLeg:
        return [Pose.ROOT_INDEX].concat(Pose.RIGHT_LEG_JOINTS);

      case BodyParts.RightArm:
        return [Pose.THORAX_INDEX].concat(Pose.RIGHT_ARM_JOINTS);
      
      case BodyParts.LeftThumb:
        return [Pose.LHAND_INDEX].concat(Pose.LEFT_THUMB_JOINTS);
        
      case BodyParts.RightThumb:
        return [Pose.RHAND_INDEX].concat(Pose.RIGHT_THUMB_JOINTS);

      case BodyParts.Torso:
        return Pose.TORSO_JOINTS;
      
      default:
        return [];
    }

  }

  getBodyPart(bodyPart: BodyParts) : Vector3[] {
    switch(bodyPart) {
      case BodyParts.Head:
        return this.getHead();

      case BodyParts.LeftLeg:
        return this.getLeftLeg();

      case BodyParts.LeftArm:
        return this.getLeftArm();
      
      case BodyParts.RightLeg:
        return this.getRightLeg();

      case BodyParts.RightArm:
        return this.getRightArm();

      case BodyParts.Torso:
        return this.getTorso();

      case BodyParts.LeftThumb:
        return this.getLeftThumb();
      
      case BodyParts.RightThumb:
        return this.getRightThumb();
      
      default:
        return [];
    }
  }

  getCoordinates() : Array<Vector3> {
    return this.coordinates;
  }

  /**
   * Calculates list of vectors, that are necessary for shifting 
   * from current pose to next pose.
   * 
   * @param nextCoordinates pose to shift to
   * 
   * @returns 
   */
  static calculateDeltaSubPose(oldCoordinates : Vector3[], nextCoordinates : Vector3[]) : Vector3[] {
    const deltaList : Vector3[] = [];
    for (let i = 0; i < oldCoordinates.length; i++) {

      deltaList.push(Pose.calculateDelta(oldCoordinates[i], nextCoordinates[i]));
    }

    return deltaList;
  }

  convertPointsToTHREEModule(points: Vector3[]) : THREE.Vector3[] {
    const newPoints : THREE.Vector3[] = [] 
    for (let i = 0; i < points.length; i++) {
      const point = points[i];
      newPoints.push(new THREE.Vector3(point.x, point.y, point.z));
    }

    return newPoints;
  }

  /**
   * Calculates vectors, that is necessary for shifting 
   * from current vec1 to vec2.
   * @param vec1 
   * @param vec2 
   */
  private static calculateDelta(vec1 : Vector3, vec2: Vector3) : Vector3 {
    return new Vector3(vec1.x - vec2.x, vec1.y - vec2.y, vec1.z - vec2.z)
  }

  getJointByIndex(index : number) {
    return this.coordinates[index];
  }

  /**
   * VERY MUCH NOT PRETTY
   * 
   * @param x 
   * @param y 
   * @param z 
   */
  translatePose(x,y,z) {
    this.coordinates.map(joint => joint.add(new Vector3(x,y,z)));
  }

  normalizeJoints(rootJoint: Vector3, joints: Vector3[]) {
    return joints.map(joint => joint.addScaledVector(rootJoint, -1));
  }

  /**
   * Normalizes limb parts in pose
   * 
   * @returns copy of pose
   * 
   */
  normalizePoseToLimbParts() : Pose {
    const cpy : Vector3[] = [];
    const poseCopy = new Pose(cpy);
    this.coordinates.map(coord => cpy.push(new Vector3(coord.x, coord.y, coord.z)));
    ALL_PARTS.map(part => this.normalizeLimb(part, poseCopy));
    return poseCopy;
  }


  private normalizeLimb(bodyPart : BodyParts, pose: Pose) {
    const indices = pose.getJointsIndices(bodyPart);
    let rootJointIndex = indices[0];
    // switch(bodyPart) {

    //   case BodyParts.LeftLeg:
    //   case BodyParts.RightLeg:
    //     rootJointIndex = Pose.ROOT_INDEX;
    //     break;

    //   case BodyParts.LeftArm:
    //   case BodyParts.RightArm:
    //   case BodyParts.Torso:
    //   case BodyParts.Head:
    //     rootJointIndex = Pose.THORAX_INDEX;
    //     break;

    //   case BodyParts.LeftThumb:
    //     rootJointIndex = Pose.LHAND_INDEX;
    //     break;
    //   case BodyParts.RightThumb:
    //     rootJointIndex = Pose.RHAND_INDEX;
    //     break;
    // }
    const offset = pose.getJointCoordinate(rootJointIndex);
    indices.map(index => {
      const current = pose.getJointCoordinate(index);
      pose.setJointCoordinate(index, new Vector3(current.x - offset.x, current.y - offset.y, current.z - offset.z));
    })
  }

  getJointCoordinate(jointIndex: number) {
    return this.coordinates[jointIndex];
  }

  setJointCoordinate(jointIndex: number, coord: Vector3) {
    this.coordinates[jointIndex] = coord;
  }
}