import { Vector3 } from 'three';
import { DoubleSide, LineCurve3, Mesh, MeshBasicMaterial, SphereGeometry, TubeGeometry } from 'three';
import { ITresholds, JointThresholdSettings } from './Entities/JointThresholdSettings';

export class SkeletonPainter {

  static BONE_RADIUS = 0.25;
  static BONE_TUBULAR_SEGMENTS = 1;
  static BONE_RADIAL_SEGMENTS = 4;
  static JOINT_RADIUS = 0.4;
  static JOINT_HEIGHT_SEGMENTS = 10;
  static JOINT_WIDTH_SEGMENTS = 10;
  static JOINT_MENTOR_MATERIAL = { color: 0x7952b3 }
  static JOINT_THRESHOLD_MATERIALS = [
    { severity: 'none', color: 0x3dab1b, transparent: true },
    { severity: 'minor', color: 0x85ab1b, transparent: true },
    { severity: 'medium', color: 0xab981b, transparent: true },
    { severity: 'major', color: 0xb00505, transparent: true }
  ]
  static JOINT_THRESHOLD_LOW_MATERIAL = { color: 0x3dab1b, transparent: true } 

  static createBones = (coordinates: Array<Vector3>) : Mesh[] => {
    const lines : Mesh[] = [];
    for (let i = 0; i < coordinates.length - 1; i++) {
      lines.push(this.makeBone(coordinates[i], coordinates[i + 1]));
    }
  
    return lines;
  }
  
  static createJoints = (coordinates : Vector3[] ) : Array<Mesh> => {
    const joints : Array<Mesh> = [];
    
    for (let i = 0; i < coordinates.length; i++) {
      joints.push(this.makeJoint(coordinates[i]));  
    }
  
    return joints;
  }
  
  
  private static makeBone = (pointA: Vector3, pointB: Vector3) : Mesh => {
  
    const path = new LineCurve3( pointA, pointB );
    //@ts-ignore
    const geometry = new TubeGeometry( path, this.BONE_TUBULAR_SEGMENTS, this.BONE_RADIUS, this.BONE_RADIAL_SEGMENTS, false );
    const material = new MeshBasicMaterial( { color: 0x7952b3, side: DoubleSide } );
    return new Mesh( geometry, material );
  }
  
  private static makeJoint = (center : Vector3) : Mesh => {
    const geometry = new SphereGeometry( this.JOINT_RADIUS, this.JOINT_HEIGHT_SEGMENTS, this.JOINT_WIDTH_SEGMENTS );
    const material = new MeshBasicMaterial( this.JOINT_MENTOR_MATERIAL );
    const mesh = new Mesh( geometry, material );
    //@ts-ignore
    mesh.position.set(center.x, center.y, center.z);
  
    return mesh
  }

  static createThresholdZones(center: Vector3, thresholdSettings : JointThresholdSettings) {

    const meshes = this.JOINT_THRESHOLD_MATERIALS.map(sphereMaterial => {
      const geometry = new SphereGeometry( thresholdSettings.tresholds[sphereMaterial.severity] * thresholdSettings.modifier, this.JOINT_HEIGHT_SEGMENTS + 10, this.JOINT_WIDTH_SEGMENTS + 10);
      const materialProperties = {color: sphereMaterial.color, transparent: sphereMaterial.transparent}
      const material = new MeshBasicMaterial( materialProperties );
  
      material.opacity = 0.2;
      const mesh = new Mesh( geometry, material );
      //@ts-ignore
      mesh.position.set(center.x, center.y, center.z);
      return mesh;
    })
   
    return meshes;

  }

}



