import { Color, LineCurve3, TubeGeometry, Mesh, MeshBasicMaterial, Scene, Vector3 } from "three";
import { getBoneColor, getJointColor } from "../PaintUtils";
import { SkeletonPainter } from "../SkeletonPainter";
import { THRESHOLD_DEFINITIONS } from "../ThresholdDefinitions";
import { ALL_PARTS, BodyParts } from "./BodyParts";
import { Pose } from "./Pose";
import { Skeleton } from "./Skeleton";

export class PaintedSkeleton {

  static thresholdZones : Mesh[][] = THRESHOLD_DEFINITIONS.map(thresholdSetting => {
    const zones = SkeletonPainter.createThresholdZones(new Vector3(0,0,0), thresholdSetting);
    return zones;
  });

  skeleton : Skeleton;
  private leftLeg : Mesh[];
  private rightLeg : Mesh[];
  private torso : Mesh[];
  private leftArm : Mesh[];
  private rightArm : Mesh[];
  private head : Mesh[];
  private leftThumb : Mesh[];
  private rightThumb : Mesh[];
  private allBones : Mesh[];
  private joints : Mesh[];
  currentFrame : number;
  translationOffset : Vector3;
  maxFrame: number;
  thresholdSphereIndex : number | undefined;

  constructor(skeleton: Skeleton) {
    const pose = skeleton.getPoses()[0];
    this.leftLeg = SkeletonPainter.createBones(pose.getLeftLeg());
    this.rightLeg = SkeletonPainter.createBones(pose.getRightLeg());
    this.torso = SkeletonPainter.createBones(pose.getTorso());
    this.leftArm = SkeletonPainter.createBones(pose.getLeftArm());
    this.rightArm = SkeletonPainter.createBones(pose.getRightArm());
    this.head = SkeletonPainter.createBones(pose.getHead());
    this.leftThumb = SkeletonPainter.createBones(pose.getLeftThumb());
    this.rightThumb = SkeletonPainter.createBones(pose.getRightThumb());
    this.allBones = [ ...this.leftLeg, ...this.rightLeg, ...this.torso, ...this.leftArm, ...this.rightArm, ...this.head, ...this.leftThumb, ...this.rightThumb];
    this.joints = SkeletonPainter.createJoints(pose.getCoordinates());
    this.currentFrame = 0;
    this.maxFrame = skeleton.getPoses().length - 1;
    this.translationOffset = new Vector3(0,0,0);
    this.skeleton = skeleton;
  }

  
  /**
   * Update skeleton to next frame
   * 
   * @returns true if animation ended
   * 
   */
  nextFrame() : boolean {

    if (this.currentFrame === this.maxFrame - 1) {
      return true;
    }

    this.currentFrame += 1;

    this.updatePose(this.skeleton.getPose(this.currentFrame));

    return this.currentFrame === this.maxFrame - 1;
  }

  updatePose(newPose : Pose ) : void {
    ALL_PARTS.map(bodyPart => {
      this.updateLimb(bodyPart, newPose);
    });
    this.allBones = [ ...this.leftLeg, ...this.rightLeg, ...this.torso, ...this.leftArm, ...this.rightArm, ...this.head, ...this.leftThumb, ...this.rightThumb];

    this.updateThresholdZonesPosition(newPose);
  }

  private updateLimb(bodyPart: BodyParts, newPose : Pose) : void {

    const nextPose = newPose.getBodyPart(bodyPart);
    const bones = this.getBodyPart(bodyPart);
    const jointsIndices = newPose.getJointsIndices(bodyPart);
    const jointsConnectedIndices = newPose.getJointsConnectedIndices(bodyPart);

    // bone update
    for (let i = 0; i < nextPose.length - 1; i++) { 

      const path = new LineCurve3(nextPose[i], nextPose[i + 1]);
      // const path = new LineCurve3(nextPose[i].add(this.translationOffset), nextPose[i + 1].add(this.translationOffset));
      //@ts-ignore
      const geometry = new TubeGeometry(path, SkeletonPainter.BONE_TUBULAR_SEGMENTS, SkeletonPainter.BONE_RADIUS, SkeletonPainter.BONE_RADIAL_SEGMENTS, false);
      const position = geometry.getAttribute('position');
      const bone = bones[i];
      bone.geometry.setAttribute("position", position);     
      geometry.dispose();

      if (this.skeleton.isPupil) {
        const jointIndex = jointsConnectedIndices[i];
        const jointIndex2 = jointsConnectedIndices[i + 1];
        const delta1 = this.skeleton.getFrameJointDelta(jointIndex, this.currentFrame);
        const delta2 = this.skeleton.getFrameJointDelta(jointIndex2, this.currentFrame);
        const color = getBoneColor(delta1, jointIndex, delta2, jointIndex2);
        //@ts-ignore
        bone.material.color = color
      }
    }

    // joint update
    for (let i = 0; i < jointsIndices.length; i++) {
      const jointIndex = jointsIndices[i];    
      const joint = this.joints[jointIndex];
      const nextJointFrame = this.skeleton.getPose(this.currentFrame).getCoordinates()[jointIndex];
      //@ts-ignore
      joint.position.set(
        nextJointFrame.x, 
        nextJointFrame.y, 
        nextJointFrame.z 
      );
     
      if (this.skeleton.isPupil) {
        const delta = this.skeleton.getFrameJointDelta(jointIndex, this.currentFrame);
        const color = getJointColor(delta, jointIndex);
        const material = joint.material as MeshBasicMaterial;
        //@ts-ignore
        material.color = color;     
      }

    }
  }

  getBodyPart(bodyPart: BodyParts) : Array<Mesh>{
    return this[bodyPart.toString()];
  }

  getAllBones(): Array<Mesh> {
    return this.allBones;
  }

  setFrame(frame: number) : void {
    this.currentFrame = frame;
  }

  /**
   * Updates skeleton to given frame.
   * 
   * @param frame to update skeleton position to
   * 
   */
  setToFrame(frame : number) : void {
    this.currentFrame = frame;
    this.updatePose(this.skeleton.getPose(frame));

  }

  getFrame() : number {
    if (this.currentFrame === this.maxFrame - 1) {
      this.currentFrame = 0;
    }
    return this.currentFrame;
  }
  
  getJoints() : Array<Mesh> {
    return this.joints;
  }

  translate(v : Vector3) {
    // this.allBones.map(bone => {
    //   bone.translateX(v.x),
    //   bone.translateY(v.y),
    //   bone.translateZ(v.z)
    // })
    this.skeleton.translateSkeleton(v.x, v.y, v.z);
    this.translationOffset = v;
  }

  addToScene(scene  : Scene ) {
    this.getAllBones().map(line => scene.add(line))
    this.getJoints().map(joint => scene.add(joint))
  }

  removeFromScene(scene: Scene) {
    this.getAllBones().map(line => scene.remove(line));
    this.getJoints().map(joint => scene.remove(joint));
  }
  
  setTranslationOffset(vec: Vector3) {
    const oldOffset = this.translationOffset;
    this.translate(new Vector3(-oldOffset.x, -oldOffset.y, -oldOffset.z));
    this.translate(vec);
  }

  getShowedThresholdZones() {
    if (this.thresholdSphereIndex) {
      return PaintedSkeleton.thresholdZones[this.thresholdSphereIndex];
    }

    return [];
  }

  updateThresholdZonesPosition(pose: Pose) {  
    if (this.thresholdSphereIndex) {

      const trackedJoint = pose.getJointByIndex(this.thresholdSphereIndex);
      const spheres = PaintedSkeleton.thresholdZones[this.thresholdSphereIndex];
      spheres.map(sphere => {
        //@ts-ignore
        sphere.position.set(
          trackedJoint.x, 
          trackedJoint.y, 
          trackedJoint.z 
        );
      });
      
    }
  }
}