export class JointThresholdSettings {

    jointIndex : number;
    tresholds : ITresholds;
    modifier : number;

    constructor(jointIndex: number, tresholds: ITresholds ) {
        this.jointIndex = jointIndex;
        this.tresholds = tresholds;
        this.modifier = 1;
    }

    getSeverity(distance: number) {

        if (distance < this.tresholds.none * this.modifier) {
          return Severity.NONE;
        }
        
        if (distance < this.tresholds.minor * this.modifier) {
          return Severity.MINOR;;
        }
    
        if (distance < this.tresholds.medium * this.modifier) {
          return Severity.MEDIUM;
        }
    
        if (distance < this.tresholds.major * this.modifier) {
          return Severity.MAJOR;
        }
    
        return Severity.ABSOLUTE;
    }


    updateThreshold(modifier: number) {
      this.modifier += modifier;
      this.modifier = Math.max(0, this.modifier);
    }

}


export interface ITresholds {
    none : number;
    minor : number;
    medium : number;
    major : number;
}

export enum Severity {
    NONE = 0,
    MINOR = 1,
    MEDIUM = 2,
    MAJOR = 3,
    ABSOLUTE = 4
  }