import {Vector3} from 'three';
import { DTWMatrixCell } from './DTWMatrixCell';
import { DTWMatrixCellWindow } from './DTWMatrixCellWindow';
import { WarpingStep } from './WarpingStep';
/**
 * Represents a DTW matrix
 * 
 * [
 *    [a1, ..., an],
 *    ...
 *    [e1, ..., en]
 * ]
 * 
 * 
 * 
 */
export class DTWMatrix {

  mentorSequence: Vector3[];
  pupilSequence: Vector3[];
  matrix: DTWMatrixCell[][]

  constructor(mentorSequence : Vector3[], pupilSequence : Vector3[]) {
    this.mentorSequence = mentorSequence;
    this.pupilSequence = pupilSequence;
    const mentorSequenceLength = mentorSequence.length + 1;
    const pupilSequenceLength = pupilSequence.length + 1;
    
    this.matrix = new Array(mentorSequenceLength);
    
    for (let i = 0; i < mentorSequenceLength; i++) {
      this.matrix[i] = new Array(pupilSequenceLength);
    }

    for (let i = 0; i < mentorSequenceLength; i++) {
      this.matrix[i][0] = new DTWMatrixCell(Infinity);
    }

    for (let i = 0; i < pupilSequenceLength; i++) {
      this.matrix[0][i] = new DTWMatrixCell(Infinity);
    }

    this.matrix[0][0].distanceValue = 0;

    this.calculateDTW();
  }

  calculateDTW() {
    for (let i = 1; i < this.mentorSequence.length + 1; i++) {
      for (let j = 1; j < this.pupilSequence.length + 1; j++) {
        this.calculateDTWMatrixCell(i , j);
      } 
    }
  }



  calculateDTWMatrixCell(i: number, j: number) {
    const jointEuclidDistance = this.calculateJointDistance(this.pupilSequence[j - 1], this.mentorSequence[i - 1]);
  
    const matrixCellWindow = new DTWMatrixCellWindow(
      this.matrix[i - 1][j - 1], 
      this.matrix[i][j - 1], 
      this.matrix[i - 1][j],
      [i,j]);

    const cellWindowMinimumResult = matrixCellWindow.findMinimalValue();
       
    const finalDistance = cellWindowMinimumResult[0] + jointEuclidDistance;
    
    const computedCell = new DTWMatrixCell(finalDistance);
    computedCell.absoluteJointDistance = jointEuclidDistance;
    computedCell.setPreviousWarping(cellWindowMinimumResult[1]);
    this.matrix[i][j] = computedCell;
  }


  calculateJointDistance(joint1 : Vector3, joint2: Vector3) {
      return Math.sqrt(Math.pow(joint1.x - joint2.x, 2) + Math.pow(joint1.y - joint2.y, 2) + Math.pow(joint1.z - joint2.z, 2));
  }

  /**
   * 
   * @returns warping path
   * 
   */
  calculateWarpingDistance() : WarpingStep[] {
    const warpingPath : WarpingStep[] = []
    let i = this.mentorSequence.length;
    let j = this.pupilSequence.length;  

    do {
      const currentCell = this.matrix[i][j];
      const warpingStep = new WarpingStep(j - 1, i - 1, currentCell.absoluteJointDistance);
      const indexes = currentCell.previousWarping;
      warpingPath.push(warpingStep)
      i = indexes[0];
      j = indexes[1];

    } while (i !== 0 && j !== 0)

    return warpingPath.reverse();
  }

}