import { GridHelper, PerspectiveCamera, Scene, Vector3, WebGLRenderer } from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { AnimationSlider } from "./AnimationSlider";
import { initSettingsButtons } from "./AnimationUtils";
import { ApplicationState } from "./Entities/ApplicationState";
import { PaintedSkeleton } from "./Entities/PaintedSkeleton";
import { SkeletonAction } from "./SkeletonAction";
import { SkeletonPainter } from "./SkeletonPainter";
import { THRESHOLD_DEFINITIONS } from "./ThresholdDefinitions";

export class CanvasScene {

    renderer : WebGLRenderer;
    controls : OrbitControls;
    camera : PerspectiveCamera;
    scene : Scene;
    gridHelper: GridHelper;
    animationSlider : AnimationSlider;
    skeletonAction : SkeletonAction;
    mentor: PaintedSkeleton;
    pupil: PaintedSkeleton;
    mentorShown: boolean;
    mentorCentered: boolean;
    normalizedLimbs: boolean;
    state: ApplicationState;
    thresholdZones;

    constructor() {
        this.state = ApplicationState.Loading;
        this.renderer = new WebGLRenderer();
        const canvasContainer = document.getElementById("canvas-container")!;
        canvasContainer.appendChild(this.renderer.domElement);
    
        const width = canvasContainer.offsetWidth; // border
        const height = canvasContainer.offsetHeight;
        
        this.renderer.setSize(width, height);
        this.scene = new Scene();
    
        this.camera = new PerspectiveCamera(
            75,
            width / height,
            0.1,
            1000
        );
    
        const orbit = new OrbitControls(this.camera, this.renderer.domElement)
        //@ts-ignore
        this.camera.position.set(0, 10, 35);
        
        orbit.update();
        this.gridHelper = new GridHelper(60);
        this.renderer.setClearColor(0xFFFFFF);
        const canvasResizer = () => {
            const canvasContainer = document.getElementById("canvas-container")!;
          
            const width = canvasContainer.offsetWidth - 1; // border
            const height = canvasContainer.offsetHeight;
            this.renderer.setSize(width, height);
            this.camera.aspect = width / height;
        }

        window.addEventListener("resize", canvasResizer);
        this.mentorShown = false;
        this.mentorCentered = false;
        this.normalizedLimbs = false;
    }

    addToScene(element : any) {
        this.scene.add(element);
    }

    initGridHelper(lowestPoint : Vector3) {
        this.gridHelper.translateY(lowestPoint.y)
        this.scene.add(this.gridHelper); 
    }

    initAnimationSlider(skeleton : PaintedSkeleton) {
        this.animationSlider = new AnimationSlider(skeleton);
    }

    initSkeletonAnimation() {
        this.skeletonAction = new SkeletonAction(this.camera, this.scene, this.renderer, this.animationSlider);
    }

    initAnimationButtons() {
        initSettingsButtons(
            {
                ...this.skeletonAction.boundFunctions, 
                switchMentor: this.switchMentor.bind(this),
                centerMentor: this.switchCenterMentor.bind(this),
                switchNormalizedLimbs: this.switchNormalizedLimbs.bind(this),
                selectThresholdHandler: this.setThresholdFromSelect.bind(this),
                selectTrackingHandler: this.setJointTrackingFromSelect.bind(this),
                manipulateThreshlodModifier: this.modifyThresholdModifier.bind(this),
                selectSpecificThreshlodHandler: this.modifyThresholdForSpecificJoint.bind(this)
            });
    }

    initAnimationControl() {
        this.animationSlider.animationControl = this.skeletonAction;
    }

    initSkeletons(pupil: PaintedSkeleton, mentor: PaintedSkeleton) {
        this.mentor = mentor;
        this.pupil = pupil;
        pupil.addToScene(this.scene);
        // mentor.addToScene(this.scene);
        // pupil.setTranslationOffset(new Vector3(-10, 0, 0));
        // mentor.setTranslationOffset(new Vector3(10, 0, 0));
        this.skeletonAction.skeletons = [pupil, mentor];
    }

    start() {
        this.skeletonAction.start();
    }

    init(pupil: PaintedSkeleton, mentor : PaintedSkeleton ) {
        this.initGridHelper(pupil.skeleton.getPose(0).getLowestPoint())
        this.initAnimationSlider(pupil);
        this.initSkeletonAnimation();
        this.initAnimationControl();
        this.initSkeletons(pupil, mentor);
        this.initAnimationButtons();
    }

    switchMentor() {
        if (this.mentorShown) {

            this.mentor.removeFromScene(this.scene);
            this.mentor.setTranslationOffset(new Vector3(0,0,0));
            this.pupil.setTranslationOffset(new Vector3(0,0,0));
            
        } else {
            
            if (this.mentorCentered) {
                this.mentor.setTranslationOffset(new Vector3(0,0,0));
                this.pupil.setTranslationOffset(new Vector3(0,0,0));
            } else {
                this.mentor.setTranslationOffset(new Vector3(10,0,0));
                this.pupil.setTranslationOffset(new Vector3(-10,0,0));
            }
            this.mentor.addToScene(this.scene)
        }
        this.mentorShown = !this.mentorShown;
    }

    switchCenterMentor() {

        if (this.mentorCentered) {
            if (this.mentorShown) {
                this.mentor.setTranslationOffset(new Vector3(10,0,0));
                this.pupil.setTranslationOffset(new Vector3(-10,0,0));
            }
            
        } else {
            this.mentor.setTranslationOffset(new Vector3(0,0,0));
            this.pupil.setTranslationOffset(new Vector3(0,0,0));
        }

        this.mentorCentered = !this.mentorCentered;
    }

    setLoaded() {
        this.state = ApplicationState.Loaded;
        const loadingOverlay = document.getElementById('loading-overlay')!;
        loadingOverlay.style.display = 'none';
        
    }

    setFailed() {
        this.state = ApplicationState.Failed;
    }

    switchNormalizedLimbs() {
        if (! this.normalizedLimbs) {
            this.pupil.skeleton.setDeltaListToNormalizedLimbs(this.mentor.skeleton);
        } else {
            this.pupil.skeleton.setDeltaListToDefault();
        }

        this.normalizedLimbs = ! this.normalizedLimbs;
        
    }

    setThresholdZone(jointIndex : number) {
        if (this.mentor.thresholdSphereIndex) {
            const zones = PaintedSkeleton.thresholdZones[this.mentor.thresholdSphereIndex];
            zones.map(zone => {
                this.scene.remove(zone);
            })
        }

        const zones = PaintedSkeleton.thresholdZones[jointIndex]
        const currentPose = this.mentor.skeleton.getPose(this.mentor.currentFrame);
        const observedJoint = currentPose.getJointByIndex(jointIndex);
        zones.map(zone => {
            //@ts-ignore
            zone.position.set(observedJoint.x, observedJoint.y, observedJoint.z)
            this.scene.add(zone);
        })
        
        this.mentor.thresholdSphereIndex = jointIndex;
    }

    setThresholdFromSelect() {

        //@ts-ignore
        const selectedIndex = document.getElementById('joint-select-threshold')!.value
        if (selectedIndex === -1) {
            this.mentor.thresholdSphereIndex = undefined;
        } else {
            this.setThresholdZone(selectedIndex);
        }
    }

    setJointTracking(jointIndex: number) {
        const warpingPath = this.pupil.skeleton.warpingPaths[jointIndex];
        this.skeletonAction.setJointTracking(warpingPath);

    }

    setJointTrackingFromSelect() {
         //@ts-ignore
         const selectedIndex = document.getElementById('joint-select-tracking')!.value
         if (selectedIndex === -1) {
             this.skeletonAction.setDefaultAnimation();
         } else {
             this.setJointTracking(selectedIndex);
         }
    }

    setThresholdModifier(modifier: number) {
        THRESHOLD_DEFINITIONS.map(threshold => {
            threshold.updateThreshold(modifier)
        })

        console.log(THRESHOLD_DEFINITIONS)
        PaintedSkeleton.thresholdZones = THRESHOLD_DEFINITIONS.map(thresholdSetting => {
            const zone = SkeletonPainter.createThresholdZones(new Vector3(0,0,0), thresholdSetting);
            return zone;
        });

    }

    modifyThresholdModifier(modifier: number) {
        const zones = this.mentor.getShowedThresholdZones();
        zones.map(zone => this.scene.remove(zone));
        this.setThresholdModifier(modifier);
        const newZones = this.mentor.getShowedThresholdZones();
        this.mentor.updateThresholdZonesPosition(this.mentor.skeleton.getPose(this.mentor.getFrame()));
        newZones.map(zone => this.scene.add(zone));
        
    }

    modifyThresholdForSpecificJoint(modifier: number) {
        //@ts-ignore
        const selectedIndex : number = document.getElementById('joint-select-specific-tracking')!.value;
        if (selectedIndex === -1) {
            return;
        }

        const oldZones = PaintedSkeleton.thresholdZones[selectedIndex];

        const thresholdSettings = THRESHOLD_DEFINITIONS[selectedIndex]
        thresholdSettings.updateThreshold(modifier);
        const newZones = SkeletonPainter.createThresholdZones(new Vector3(0,0,0), thresholdSettings);
        PaintedSkeleton.thresholdZones[selectedIndex] = newZones;
        if (selectedIndex === this.mentor.thresholdSphereIndex) {
            oldZones.map(zone => this.scene.remove(zone));
            this.mentor.updateThresholdZonesPosition(this.mentor.skeleton.getPose(this.mentor.getFrame()));
            newZones.map(zone => this.scene.add(zone));
        }
        

    }


}