import { DTWMatrixCell } from "./DTWMatrixCell";

export class DTWMatrixCellWindow {
  leftUpper : DTWMatrixCell;
  leftBottom : DTWMatrixCell;
  rightUpper : DTWMatrixCell;
  cellIndex : [number, number]

  constructor(cell1, cell2, cell3, cellIndex) {
      this.leftUpper = cell1;
      this.leftBottom = cell2;
      this.rightUpper = cell3;
      this.cellIndex = cellIndex;
  }

  /**
   * Computes minimal value of cell window
   * 
   * @returns tuple - at first position the minimal cell, at second tuple of indexes
   * 
   */
  findMinimalValue() : [number, [number, number]] {
    const minPreviousValue = Math.min(this.leftBottom.distanceValue, this.leftUpper.distanceValue, this.rightUpper.distanceValue);

    const i = this.cellIndex[0];
    const j = this.cellIndex[1];
    if (minPreviousValue === this.leftUpper.distanceValue) {
      return [this.leftUpper.distanceValue, [ i - 1, j - 1]];
    }
    
    if (minPreviousValue === this.leftBottom.distanceValue) {
        return [this.leftBottom.distanceValue, [ i , j - 1]];
    } else 

    return [this.rightUpper.distanceValue, [ i - 1, j]];
  }


}