import { Clock, PerspectiveCamera, Scene, WebGLRenderer } from "three";
import { AnimationSlider } from "./AnimationSlider";
import { getJointFrameTracking } from "./DTWCalculator";
import { PaintedSkeleton } from "./Entities/PaintedSkeleton";
import { WarpingStep } from "./Entities/WarpingStep";

const clock = new Clock();

class SkeletonAction {

  private camera : PerspectiveCamera;
  private scene : Scene;
  private renderer : WebGLRenderer;
  private interval: number; 
  private delta: number; // time elapsed since last frame
  private speedModifier: number;
  private progressionBar : AnimationSlider;
  skeletons: PaintedSkeleton[];
  private maxFrame : number;
  currentFrame : number;
  boundFunctions;
  playing : boolean;
  trackJointMovement: boolean
  jointFrames: number[];
  tickFunction : Function;

  constructor(camera: PerspectiveCamera, scene : Scene, renderer : WebGLRenderer, progressionBar : AnimationSlider, fps = 60) {
    this.camera = camera;
    this.scene = scene;
    this.renderer = renderer;
    this.speedModifier = 1;
    this.interval = (1 / fps) / this.speedModifier;
    this.delta = 0;
    this.progressionBar = progressionBar
    progressionBar.animationControl = this;
    this.maxFrame = 0;
    this.currentFrame = 0;
    this.boundFunctions = { play: this.start.bind(this), playFromStart: this.startFromBeginning.bind(this), speed: this.setSpeed.bind(this), pause: this.stop.bind(this), setFrame: this.setFrame.bind(this)}
    this.playing = false;
    this.jointFrames = [];
    this.tickFunction = this.tick.bind(this);

    window.addEventListener('keydown', this.handleSpacebar.bind(this));
  }

  startFromBeginning() {
    this.skeletons.map((skeleton) => {
      skeleton.setToFrame(0);
    });
    this.currentFrame = 0;
    this.start();
  }

  addSkeleton(skeleton : PaintedSkeleton) {
    this.maxFrame = Math.max(skeleton.maxFrame, this.maxFrame);
    this.skeletons.push(skeleton)
  }

  setFrame(frame : number) {
    this.skeletons.map((skeleton) => {
      const frameToSet = Math.min(skeleton.maxFrame, frame);
      skeleton.setToFrame(frameToSet);
    });
    this.currentFrame = frame;
  }

  render() {
    this.renderer.render(this.scene, this.camera);
  }

  start() {
    const ev = new CustomEvent("play", { detail: this.boundFunctions });
    this.playing = true;
    dispatchEvent(ev);
    this.renderer.setAnimationLoop(() => {

      this.delta += clock.getDelta();
      if (this.delta > this.interval) {
        
        if (this.tickFunction()) {
          this.stop();
          const ev = new CustomEvent("animation-end", { detail: this.boundFunctions });
          dispatchEvent(ev);
          return;
        }
        
        this.delta = this.delta % this.interval;
      }
    });
  }

  stop() {
    const ev = new CustomEvent("pause", { detail: this.boundFunctions });
    this.playing = false;
    dispatchEvent(ev);
    console.log(this.currentFrame);
    this.renderer.setAnimationLoop(() => {
      this.renderer.render(this.scene, this.camera);
    });
  }

  tick() {
    this.setFrame(this.currentFrame);
    this.currentFrame += 1;
    this.progressionBar?.animateSlider(this.skeletons[0].getFrame())

    this.renderer.render(this.scene, this.camera);

    return this.skeletons.every(skeleton => skeleton.maxFrame <= this.currentFrame);
  }

  tickJointTracker() {
    const pupil = this.skeletons[0];
    pupil.setToFrame(this.currentFrame);
    this.skeletons[1].setToFrame(this.jointFrames[this.currentFrame]);
    this.currentFrame += 1;

    this.progressionBar?.animateSlider(this.skeletons[0].getFrame())

    this.renderer.render(this.scene, this.camera);
    
    return pupil.maxFrame <= this.currentFrame;

  }

  setSpeed(modifier: number) {
    this.interval = (this.interval * this.speedModifier) / modifier;
    this.speedModifier = modifier;
  }

  setJointTracking(warpingPath: WarpingStep[]) {
    this.jointFrames = getJointFrameTracking(warpingPath);
    this.tickFunction = this.tickJointTracker.bind(this);
  } 

  setDefaultAnimation() {
    this.tickFunction = this.tick.bind(this);
  }

  handleSpacebar(e : any) {
    if (e.key == " " ||
        e.code == "Space" ||      
        e.keyCode == 32      
    ) {
      if (this.playing) {
        this.stop();
      } else {
        this.start();
      }
    }
  }
}



export { SkeletonAction };