import { Skeleton } from "./Entities/Skeleton";
import { Pose } from "./Entities/Pose";
import { SequenceParser } from "./SequenceParser";
import { PaintedSkeleton } from "./Entities/PaintedSkeleton";

export class SkeletonLoader {

    static BASE_URL = "http://disa.fi.muni.cz/mocap-demo-external/exportMocapData"
    parser: SequenceParser;
    

    constructor() {
        this.parser = new SequenceParser();
    }

    loadDummyPose(dummyFile : string[]) {
        return dummyFile.map(line => this.parser.parsePose(line))
    }

    async createPaintedSkeleton() {
        const poses = await this.loadPoses();
        const mentorSkeleton = new Skeleton(poses[0], false);
        const pupilSkeleton = new Skeleton(poses[1], true);
        pupilSkeleton.calculateDeltaFromMentor(mentorSkeleton);

        const mentor = new PaintedSkeleton(mentorSkeleton);
        const pupil = new PaintedSkeleton(pupilSkeleton);
        return [mentor, pupil]
    } 

    async loadPoses() {
        const searchString = location.search;
        const keys = searchString.substring(1, searchString.length);
        const paramsMap = keys.split('&').reduce((p, c) => {
            let components = c.split('=');
            p[components[0]] = components[1]
            return p;
        }, new Map<string, string>());

        const pupilQueryString = this.createQueryParams(paramsMap['qId'], paramsMap['qFrom'], paramsMap['qTo'], 'true');
        const mentorQueryString = this.createQueryParams(paramsMap['rId'], paramsMap['rFrom'], paramsMap['rTo'], 'true');
        const pupilResponse = fetch(SkeletonLoader.BASE_URL + pupilQueryString);
        console.log("I fetched pupil")
        const mentorResponse = fetch(SkeletonLoader.BASE_URL + mentorQueryString);
        console.log("I fetched mentor")
        const boundParser = this.parsePoseSequences.bind(this);

        const pupilPoses = pupilResponse.then( res => res.text().then(boundParser))
        const mentorPoses = mentorResponse.then( res => res.text().then(boundParser));

        const pupilResult = await pupilPoses as unknown as Pose[];
        const mentorResult = await mentorPoses as unknown as Pose[];

        return [pupilResult, mentorResult];
        
    }

    createQueryParams(sequenceLocator : string, frameFrom: string, frameTo: string, normalize: string) : string {
        return `?sequenceLocator=${sequenceLocator}&from=${frameFrom}&to=${frameTo}&normalize=${normalize}`

    }

    parsePoseSequences(text : string) : Pose[] {
        console.log('parsing start')
        const bits = text.split('\n');
        const lines = bits.splice(1, bits.length - 3);
        const parsed = lines.map(line => this.parser.parsePose(line));
        console.log('parsing ends')
        
        return parsed;
        
    }

}
