import { Color } from "three";
import { THRESHOLD_DEFINITIONS } from "./ThresholdDefinitions";

const BONE_COLORS = [ 
  new Color(0x3dab1b),
  new Color(0x7dab1b),
  new Color(0x85ab1b),
  new Color(0xa1ab1b),
  new Color(0xab981b),
  new Color(0xab791b),
  new Color(0xb00505),
  new Color(0x660000),
  new Color(0x000000)
];

export const JOINT_COLORS = [ 
  new Color(0x3dab1b),
  new Color(0x85ab1b),
  new Color(0xab981b),
  new Color(0xb00505),
  new Color(0x000000)
]

export const getJointColor = (delta: number, jointIndex: number) => {
  const severity = THRESHOLD_DEFINITIONS[jointIndex].getSeverity(delta);
  return JOINT_COLORS[severity];
}

export const getBoneColor = (delta1: number, jointIndex1: number, delta2: number, jointIndex2: number) => {
  
  const jointDiffLevel1 = THRESHOLD_DEFINITIONS[jointIndex1].getSeverity(delta1);
  const jointDiffLevel2 = THRESHOLD_DEFINITIONS[jointIndex2].getSeverity(delta2);
  
  const diffLevel = jointDiffLevel1 + jointDiffLevel2;

  return BONE_COLORS[diffLevel];
}


