import { Vector3} from "three";
import { calculateDTW, calculateDTWOnPoses, calculateWarpingPathDeltas } from "../DTWCalculator";
import { BodyParts } from "./BodyParts";
import { Pose } from "./Pose";
import { WarpingStep } from "./WarpingStep";

export class Skeleton {

  private poses: Array<Pose>;
  private limbNormalizedPoses : Array<Pose>;
  deltaList : number[][];
  deltaListNormalized: number[][];
  activeDeltaList: number[][];
  isPupil : boolean;
  warpingPaths : WarpingStep[][];
  
  constructor(poses: Pose[], isPupil: boolean) {
    this.poses = poses;
    this.limbNormalizedPoses = [];
    this.deltaList = [];
    this.deltaListNormalized = [];
    this.activeDeltaList = this.deltaList;
    this.isPupil = isPupil;
    this.warpingPaths = []
  }


  setPoses(poses: Array<Pose>) : void {
    this.poses = poses;
  }

  getJointTimeSequence(index: number) : Vector3[]{
    return this.poses.map(pose => pose.getJointByIndex(index));
  }

  getPoses() {
    return this.poses;
  }

  getPose(index : number) {
    return this.poses[index];
  }

  /**
   * TODO: refactor this
   * 
   */
  translateSkeleton(x, y, z) {
    this.poses.map(pose => pose.translatePose(x,y,z))

  }

  getJointDelta(jointIndex : number) {
    return this.activeDeltaList[jointIndex];

  }

  getFrameJointDelta(jointIndex : number, frame : number) {

    return this.activeDeltaList[jointIndex][frame];

  }

  calculateDeltaFromMentor(mentor: Skeleton) {
    this.warpingPaths = calculateDTW(mentor, this);
    this.deltaList = calculateWarpingPathDeltas(this.warpingPaths);
    this.activeDeltaList = this.deltaList;
  }

  setDeltaListToNormalizedLimbs(mentor: Skeleton) {
    if (this.deltaListNormalized.length == 0) {
      this.limbNormalizedPoses = this.poses.map(pose => pose.normalizePoseToLimbParts());
      const mentorNormalizedPoses = mentor.poses.map(pose => pose.normalizePoseToLimbParts());
  
      this.deltaListNormalized = calculateWarpingPathDeltas(calculateDTWOnPoses(mentorNormalizedPoses, this.limbNormalizedPoses));
      
    }
    this.activeDeltaList = this.deltaListNormalized;

  }

  setDeltaListToDefault() {
    this.activeDeltaList = this.deltaList;
  }

  getSwitchNormalizedLimbsFunction() {
    return this.setDeltaListToNormalizedLimbs.bind(this);
  }

}